.Employee-sign-in-and-sign-out {
  width: 850px;
  display: flex;
  justify-content: space-between;
  margin: 30px auto;
  height: calc(100vh - 264px);
}

@media screen and (max-width: 768px) {
  .Employee-sign-in-and-sign-out {
    width: 100%;
    height: calc(100vh - 350px);
    flex-direction: column;
    align-items: center;
  }
}
